import { Button } from "antd";
import React from "react";
import { Icon as LegacyIcon } from "@ant-design/compatible";

const CButtonReport = ({ type, icon, onClick, label, ...rest }) => {
  return (
    <Button
      type={type}
      icon={<LegacyIcon type={icon} />}
      size="small"
      onClick={onClick}
      {...rest}
    >
      {label}
    </Button>
  );
};

export default CButtonReport;
